<script>
export default {
  data() {
    return {
      loading: false,
      confirmData: {
        towerId: '',
        levelId: '',
        towerName: '',
        levelName: '',
        time: '',
        price: 0,
        totalPrice: 0,
        name: '',
        phone: '',
        idCard: '',
        wish: '',
