  },
  onLoad() {
    // ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñÊï∞ÊçÆ
    const data = uni.getStorageSync('lightOrder')
    if (data) {
      this.confirmData = { ...this.confirmData, ...data }
    } else {
      // Â¶ÇÊûúÊ≤°ÊúâÊï∞ÊçÆÔºåËøîÂõû‰∏ä‰∏ÄÈ°µ
      uni.showToast({ 
