  methods: {
    submitOrder() {
      this.loading = true
      
      // Ê®°ÊãüÊèê‰∫§ËÆ¢Âçï
      setTimeout(() => {
        this.loading = false
        
        // ‰øùÂ≠òËÆ¢Âçï‰ø°ÊÅØ
        const orderData = {
          ...this.confirmData,
          orderId: 'L' + Date.now(),
          status: 'pending',
          createTime: new Date().toISOString()
        }
        
        // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
        const orders = uni.getStorageSync('lightOrders') || []
        orders.unshift(orderData)
        uni.setStorageSync('lightOrders', orders)
        
        // Ê∏ÖÈô§‰∏¥Êó∂Êï∞ÊçÆ
        uni.removeStorageSync('lightOrder')
        
        uni.showToast({ 
          title: 'Êèê‰∫§ÊàêÂäü', 
          icon: 'success',
          duration: 1500
        })
        
        setTimeout(() => {
          // Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÈ°µÈù¢
          uni.navigateTo({
            url: '/pages/light/pay',
            success: () => {
              console.log('Ë∑≥ËΩ¨Âà∞ÊîØ‰ªòÈ°µÈù¢ÊàêÂäü')
            },
            fail: (err) => {
              console.error('Ë∑≥ËΩ¨ÊîØ‰ªòÈ°µÈù¢Â§±Ë¥•:', err)
              // Â¶ÇÊûúË∑≥ËΩ¨Â§±Ë¥•ÔºåË∑≥ËΩ¨Âà∞ÊàêÂäüÈ°µÈù¢
              uni.navigateTo({
